@font-face {
  font-family: FontawesomeSolid;
  src: url('https://kit-free.fontawesome.com/algo/1/webfonts/fa-solid-900-free-5.5.0.woff2')
    format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: FontawesomeRegular;
  src: url('https://kit-free.fontawesome.com/algo/1/webfonts/fa-regular-400-free-5.5.0.woff2')
    format('woff2');
  font-weight: 400;
  font-style: normal;
}

.weather {
  overflow: hidden;
  line-height: 1.15;
  font-size: 15x;
  display: flex;
  flex-direction: column;
}
.weather .flexb {
  width: 50%;
  height: 50%;
  margin: 0.5rem auto;
  border: 1px solid #1abc9c;
}
.weather tr {
  color: white;
  background: #28e1bd;
  display: grid;
  overflow: hidden;
  grid-template-columns: 0.5fr 1fr;
}
.weather tr:hover {
  background: #138b73;
}
.weather td {
  padding: 0 0.25rem;
  text-align: center;
}
.weather .col {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}
.weather .col:hover {
  color: #1abc9c;
}
.weather .date-style {
  font-family: sans-serif;
}
.weather .date-style:hover {
  color: #28e1bd;
}
.weather .date {
  font-size: 1.5em !important;
  text-transform: uppercase;
  color: #0e6252;
}
.weather .day {
  text-transform: uppercase;
  display: block;
  overflow: hidden;
}

@media (min-width: 501px) {
  .weather {
    position: absolute;
    top: 0;
    left: 0;
    max-height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: row;
  }
  .weather .flexb {
    margin: 0.5rem 0.25rem;
    overflow-y: hidden;
  }
  .weather tr {
    display: flex;
    flex-direction: column;
  }
  .weather .col {
    flex-direction: column;
    display: flex;
    flex-wrap: wrap;
  }
  .weather .day {
    font-size: 80%;
  }
}

#req1 {
  display: none;
}

.temperature {
  font-size: 1.4em;
  margin: 1rem 0;
}
.temperature:before {
  content: '\f76b';
  color: #0e6252;
  display: block;
}
.temperature:hover {
  color: #28e1bd;
}

*:before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: FontawesomeSolid, FontawesomeRegular;
}
